import React from 'react';

import { Card, CardTitle, CardText, CardActions, Button } from 'react-mdl';

const ProjectCard = (props) => {

	var header = style.alt_header;
	if (props.img != null) {
		let img = `url(${props.img})`;
		header = {
			background: `${img} center top/ contain no-repeat`,
			minHeight: '70%'
		};
	}
	else{
		console.log("Img prop null")
	}
	
	return (
		<Card
			shadow={5}
			style={{
				minWidth: '370px',
				maxWidth: '400px',
			
				maxHeight: '600px',
				margin: '8px'
			}}
		>
			<Card
				style={{
					width: '100%',
					height: '100%'
				}}
			>
				<CardTitle expand style={header}>
					<p style={style.title}>{props.title}</p>
				</CardTitle>

				<CardText style={style.desc}>{props.des}</CardText>
			</Card>

			<CardActions border>
				<Button  colored>
					<a style={{ "text-decoration": "none", color: "#0251da" }} href={props.link} rel="noopener noreferrer" target="_blank">
						GitHub
					</a>
					
				</Button>
			</CardActions>
		</Card>
	);
};

const style = {
	root: {},
	title: {
		fontSize: '1.5rem',
		backgroundColor: '#100d2b',
		color: '#f48f50',
		position: 'absolute',
		top: '60%',
		opacity: '.9',
		left: '0px',
		width: '80%',
		margin: 'auto',
		padding: '15px',
		fontFamily: 'Judson sans-serif'
	},
	desc: {
		fontSize: '16px',
		height: '700px',

		margin: 'auto',
		marginTop:'10px',
		alignText: 'justify',
		justifyContent: 'center',
		fontFamily: 'Judson sans-serif'
	},
	alt_header: {
		background: `url("assets/images/waitinng.jpg") center top/ 100% 100% no-repeat`,
		
	}
};

export default ProjectCard;
