import React, { Component } from 'react';

class Resume extends Component {
	render() {
		let url = 'https://drive.google.com/file/d/1pxWqi-1LB3HdWqKeuWEhOk1_3bl64T3R/view';
	
		return (
			<a href={url} rel="noopener noreferrer" target="_blank">
				<div>
					<i aria-hidden="true" />
				</div>
			</a>
			// <div style={{ background: '#fff', width: '100%', height: '100%' }}>
			// 	<h1>Resume</h1>

			// 	<iframe style={{ background: '#afafaf', width: '100%', height: '100%' }} src={url} />

			// </div>
		);
	}
}

export default Resume;
