import React from 'react';
import { Nav, Navbar, NavDropdown, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

function NavBar(props) {
    let url = 'https://drive.google.com/file/d/1pxWqi-1LB3HdWqKeuWEhOk1_3bl64T3R/view';
   
    return (
        <Navbar collapseOnSelect={true} variant="light" className="container-fluid header text-uppercase" expand="lg md">
           
            <Link className="header-title" to="/">
                <h1 className="h1"> Zain Tanveer |</h1>
			</Link>
          
            <Navbar.Toggle className="bg-primary" aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav" className="justify-content-center">
                <Nav id="basic-navbar-nav" className="justify-content-center">
                    <Nav.Item className="navItem rounded">
                        <a href={url} rel="noopener noreferrer" target="_blank">
                            <p>Resume</p>
                        </a>
                    </Nav.Item>
                    <Nav.Item className="navItem nav-pills rounded">
                        <Link  to="/projects">
                            <p>Projects</p>
                        </Link>

                    </Nav.Item>
                    <Nav.Item className="navItem rounded">
                        
                        <Link to="/contact">
                            <p>Contact</p>
                        </Link>
                    </Nav.Item>
                </Nav>
              
                
            </Navbar.Collapse>
        </Navbar>
    );
}
export default NavBar;
