import React, { Component } from 'react';
import { Tabs, Tab } from 'react-mdl';
import { ProjectCard } from './presentations';

/* Projects Details*/

import { Details } from 'assets/const';


class Projects extends Component {
	constructor(props) {
		super(props);
		this.state = { activetabs: 0 };
	}
	getNoOfCards(N, projects) {
		let noOfCards = [];
		for (let i = 0; i < N; i++) {
			let project = projects[i];
			noOfCards.push(<ProjectCard 
				title={project[0]} 
				des={project[1]} 
				img={project[2]} link={project[3]}/>);
		}
		return noOfCards;
	}
	toggleCategories() {
		let noOfCards = [];
		let projectId = this.state.activetabs;
		let totCategory = Object.keys(Details).length;
		console.log(totCategory);
		let projects = null;
		let N = null;
		if (projectId === 0) {
			for (let i = 1; i <= totCategory; i++) {
				projects = Details[i];
				N = projects.length;
				noOfCards.push(this.getNoOfCards(N, projects));
			}
		} else {
			projects = Details[projectId];
			N = projects.length;
			noOfCards.push(this.getNoOfCards(N, projects));
		}

		return <div className="projects-grid">{noOfCards}</div>;
	}

	render() {
		return (
			<div className="category-tabs">
				<Tabs id="tabs"
					activetabs={this.state.activeTabs}
					onChange={(tabId) => this.setState({ activetabs: tabId })}
					ripple
				>
					<Tab id="tabs">All</Tab>
					<Tab id="tabs">ReactJs</Tab>
					<Tab id="tabs">Android</Tab>
					<Tab id="tabs">ReactNative</Tab>
					<Tab id="tabs">Python</Tab>
					<Tab id="tabs">Web</Tab>
					<Tab id="tabs">Data Sceince</Tab>
				</Tabs>
				<section>{this.toggleCategories()}</section>
			</div>
		);
	}
}

export default Projects;
